body {
  font-family: "Hiragino Mincho Pro", serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.article {
  margin-top: 5.875rem;
  margin-bottom: 7.5625rem;
}

.article__inner {
  display: flex;
  flex-direction: column;
}

.article__wrap {
  margin-top: 3.4375rem;
}

.article__category span {
  display: inline-block;
  background-color: #11315f;
  color: #fff;
  padding: 0.5625rem 1.875rem;
  font-size: 0.875rem;
}

.article__title {
  margin-top: 1.5625rem;
  font-size: 1.875rem;
}

.article__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 1.5625rem;
}

.article__sns-wrap {
  display: inline-block;
  padding: 0.625rem 0.75rem;
  background-color: #00ACED;
  color: #fff;
  text-transform: capitalize;
  font-size: 1.25rem;
  font-weight: 700;
}

.article__sns i {
  width: 1.5625rem;
  margin-right: 0.625rem;
}

.article__date {
  font-size: 1rem;
}

.article__image {
  margin-top: 1.875rem;
}

.article__header1 {
  margin-top: 1.875rem;
  background-color: #11315f;
  color: #fff;
  font-size: 1.75rem;
  padding: 1.125rem 1.25rem;
}

.article__header2 {
  margin-top: 2.5rem;
  font-size: 1.625rem;
  padding: 0.625rem 1.3125rem;
  border-left: 0.125rem solid #2988b5;
}

.article__text1,
.article__text2 {
  margin-top: 2.5rem;
}

.article__related {
  margin-top: 3rem;
}

.article__recommend {
  margin-top: 2.75rem;
}