@use "global" as *;

.article{
  margin-top: rem(94);
  margin-bottom: rem(121);
}

.article__inner{
  display: flex;
  flex-direction: column;
}

.article__wrap{
  margin-top: rem(55);
}

.article__category span{
  display: inline-block;
  background-color: $dark-blue;
  color: $white;
  padding: rem(9) rem(30);
  font-size: rem(14);
}

.article__title{
  margin-top: rem(25);
  font-size: rem(30);
}

.article__info{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: rem(25);
}

.article__sns-wrap{
  display: inline-block;
  padding: rem(10) rem(12);
  background-color: #00ACED;
  color: $white;
  text-transform: capitalize;
  font-size: rem(20);
  font-weight: $bold;
}

.article__sns i{
  width: rem(25);
  margin-right: rem(10);
}

.article__date{
  font-size: rem(16);
}

.article__image{
  margin-top: rem(30);
}

.article__header1{
  margin-top: rem(30);
  background-color: $dark-blue;
  color: $white;
  font-size: rem(28);
  padding: rem(18) rem(20);
}

.article__header2{
  margin-top: rem(40);
  font-size: rem(26);
  padding: rem(10) rem(21);
  border-left: rem(2) solid $blue;
}

.article__text1,
.article__text2{
  margin-top: rem(40);
}

.article__sidebar{}

.article__related{
  margin-top: rem(48);
}

.article__recommend{
  margin-top: rem(44);
}